/* DEFAULT */

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Inter", sans-serif;
  font-family: "Karla", sans-serif;
  font-size: 16px;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  background-color: F5F7FB;
  color: #293264;
  width: 100%;
  min-height: 100vh;
  display: grid;
  place-items: center;

  /* BG IMG */
  background: url(../public/images/blobsBlueCut.png) bottom left no-repeat,
    url(../public/images/blobsYellowCut.png) top right no-repeat;
}

/* PROJECT STYLES START */

/* START PAGE */

.start-page {
  display: grid;
  place-items: center;
}

.start-page h1 {
  font-weight: 700;
  margin-bottom: 7px;
}

.start-page p {
  font-weight: 400;
  margin-bottom: 29px;
  line-height: 19px;
}

.btn {
  width: 193px;
  height: 52px;
  color: #f5f7fb;
  background-color: #4d5b9e;
  border: none;
  border-radius: 20px;
  font-family: Inter;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  cursor: pointer;
  text-align: center;
}

/* QUESTION COMONENT */

.quiz-page {
  display: flex;
  flex-direction: column;
}

.quiz-frame {
  padding: 20px;
  margin-bottom: 2px;
}

.question {
  font-family: Karla;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: #293264;
}

.answers {
  margin-top: 20px;
  padding-bottom: 20px;
  display: flex;
  justify-content: flex-start;
  border-bottom: 0.794239px solid #dbdef0;
}

.answer {
  border: 1px solid #4D5B9E;
  padding: 4px 15px;
  border-radius: 8px;
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 10.24px;
  line-height: 12px;
  text-align: center;
  cursor: pointer;
  color: #293264;
  background-color: transparent;
}

.answer:not(:last-child) {
  margin-right: 10px;
}

.quiz-footer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.quiz-footer p {
  margin-right: 20px;
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 12.8px;
  line-height: 15px;
  text-align: center;

  color: #293264;
}

.btn--check {
  align-self: center;
}
